.svg-icon {
  width: 20px;
  height: 20px;
  fill: #fff;
  position: relative;
  top: 2px;
  padding-right: 3px;
}
.count {
  font-size: 12px;
  color: rgb(12, 5, 5);
  position: relative;
  top: 2px;
  margin-right: 5px;
  padding-top: 5px;
}
.count-like {
  font-size: 12px;
  color: rgb(12, 5, 5);
  position: relative;
  top: 2px;
  margin-right: 5px;
  padding-bottom: 5px;
}
@media (max-width: 768px) {
  .count {
    font-size: 10px;
    color: rgb(12, 5, 5);
    position: relative;
    top: 2px;
    margin-right: 5px;
    padding-top: 5px;
  }
  .count-like {
    font-size: 10px;
    color: rgb(12, 5, 5);
    position: relative;
    top: 2px;
    margin-right: 5px;
    padding-bottom: 5px;
  }
  .svg-icon {
    padding-right: 5px;
  }
}

h1 {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI Adjusted', 'Segoe UI', 'Liberation Sans',
    sans-serif;
  font-size: 27px;
  line-height: 17px;
  vertical-align: baseline;
  letter-spacing: normal;
  word-spacing: 0px;
  margin-top: 5px;
  text-decoration: none;
}
.firstName {
  font-weight: 400;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  text-align: start;
  text-indent: 0px;
  color: #555555;
}
.lastName {
  color: #6667ab;
}
